import { createWriteStream } from 'streamsaver';

const FS_SIZE = 4 * 1024 * 1024 * 1024;

export default class Writer {

  readonly sampleRate: number;
  readonly bitDepth: number;
  readonly channels: number;

  private stream: WritableStream | null = null;

  constructor(sampleRate: number, bitDepth: number, channels: number) {
    this.sampleRate = sampleRate;
    this.bitDepth = bitDepth;
    this.channels = channels;
  }

  request(): void {
    this.stream = createWriteStream('out.pcm');
  }

  feed(samples: Buffer): void {
    this.stream?.getWriter().write(samples.buffer);
  }

  finish(): void {
    this.stream?.getWriter().close();
  }

}
